import {LitElement, html, css, customElement} from 'lit-element';
import {isXs} from '../../../utils/response-query';

@customElement('uc-plp-item-rating')
class UcPlpItemRating extends LitElement {
    static get properties() {
        return {
            rating: { type: String },
            count: { type: String }
        };
    }

    static styles = css`
        .rating-wrapper {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex; }
        @media (max-width: 767px) {
        .rating-wrapper {
                -webkit-box-pack: justify;
                -ms-flex-pack: justify;
                justify-content: space-between; } }
        
        @media (min-width: 768px) {
        :host-context([typed="card"]) .rating-wrapper {
                -webkit-box-pack: justify;
                -ms-flex-pack: justify;
                justify-content: space-between; } }
        
        .stars {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            justify-content: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center; }
        @media (min-width: 768px) {
        .stars {
                margin-right: 18px; } }
        @media (max-width: 767px) {
        .stars {
                margin-right: 1.28571em; } }
        
        .star {
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
            display: block; }
        @media (min-width: 768px) {
        .star {
                width: 12px;
                height: 11px;
                margin-right: 2px; } }
        @media (max-width: 767px) {
        .star {
                width: 0.85714em;
                height: 0.78571em;
                margin-right: 0.14286em; } }
        
        .on {
            background-image: url(data:image/svg+xml;base64,); }
        
        .off {
            background-image: url(data:image/svg+xml;base64,); }
        
        .half {
            background-image: url(data:image/svg+xml;base64,); }
        
        .reviews {
            color: #66c05d;
            text-decoration: none;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            justify-content: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center; }
        @media (min-width: 768px) {
        .reviews {
                font-size: 13px; } }
        @media (max-width: 767px) {
        .reviews {
                font-size: 0.78571em; } }
        
        .reviews-icon {
            display: inline-block;
            background-image: url(data:image/svg+xml;base64,);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center; }
        @media (min-width: 768px) {
        .reviews-icon {
                width: 18px;
                height: 15px;
                margin-right: 4px; } }
        @media (max-width: 767px) {
        .reviews-icon {
                width: 1.63636em;
                height: 1.36364em;
                margin-right: 0.36364em; } }
    `;

    constructor() {
        super();
        this.rating = '0';
        this.count = '0';
    }

    render() {
        //TODO: define algorithm create stars

        return html`
            <div class="rating-wrapper">
                <div class="stars">
                    <div class="star on"></div>
                    <div class="star on"></div>
                    <div class="star half"></div>
                    <div class="star off"></div>
                    <div class="star off"></div>
                </div>
                <a class="reviews" href="#reviews">
                    <span class="reviews-icon"></span>
                    <span class="reviews-text">
                        ${ isXs() ? html`(${this.count})` : html`${this.count} отзывов`}
                    </span>
                </a>
            </div>
        `;
    }
}